header{
  display: flex;
  justify-content: space-between;
  background: grey;
}

header a{
  color: white;
  text-decoration: none;
}
nav ul{
  display: flex;
  list-style-type: none;
}
nav ul a{
  padding: 8px 16px;
  text-decoration: none;
}
.logo{
  padding: 8px 16px;
}
.case-combination, .color{
  height: 20px;
  width: 20px;
  border: 1px solid black;
  border-radius: 10px;
  margin: 0px 2px;
}
.case-result{
  height: 10px;
  width: 10px;
  border: 1px solid black;
  border-radius: 10px;
}

.line, .combination, .result{
  display: flex;
  width: 100%;
  height: 100%;
  padding: 8px;
}
.action{
  margin-top: 10px;
}
.red{
  background: red;
}
.blue{
  background: blue;
}
.green{
  background: green;
}
.orange{
  background: orange;
}
.purple{
  background: purple;
}
.yellow{
  background: yellow;
}
main{
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.line{
  border: gray 1px outset;
  display: flex;
  width: 200px;
  justify-content: center;
  margin: 0px 16px;
}
button{
  margin: 8px 8px;
  color: white;
  font-size: 16px;
  outline: none;
  border: 1px solid black;
  background-color: rgba(0, 0, 0, 0.283);
}
button:hover{
  background-color: black;
  transition-duration: 1s;
}
.gray{
  background: gray;
}
.black{
  background: black;
}

.hidden {
  display: none;
}

.show {
  display: flex;
}
.modal{
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
}

.modalcontent {
  background: white;
  padding: 10px;
}
.bg-gray{
  background: rgba(128, 128, 128, 0.652);
}
.plateau{
  display: flex;
  flex-direction: column-reverse;
}
